@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;700&display=swap);
body{margin:0}

.humidity{padding:15px;max-height:200px;background:#1E213A;text-align:center;margin:10px;color:#E7E7EB}.humidity *{margin:0}.humidity h3{font-weight:500}.humidity__value{font-size:5rem;margin:20px}.humidity__value-thin{font-size:4rem}.humidity__visual{margin:auto;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-content:center;align-content:center;-webkit-justify-content:center;justify-content:center;width:75%;color:#A09FB1}.humidity__scale{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;margin:0}.humidity__whole{padding:0;width:100%;height:12px;border-radius:6px;background:#E7E7EB;overflow:hidden}.humidity__progress{height:100%;width:50px;background:#FFEC65}.humidity__unit{text-align:right;margin:0}@media (min-width: 700px){.humidity{width:42%}}@media (min-width: 1000px) and (max-width: 1300px){.humidity{width:90%}}

.highlight{padding:15px;max-height:200px;background:#1E213A;text-align:center;margin:10px;color:#E7E7EB}.highlight *{margin:0}.highlight h3{font-weight:500}.highlight__value{font-size:4.5rem;font-weight:700;margin:20px}.highlight__value-unit{margin-left:7px;font-size:2.5rem;font-weight:500}.highlight__unit{text-align:right;margin:0}@media (min-width: 700px){.highlight{min-width:42%}}@media (min-width: 1000px) and (max-width: 1300px){.highlight{width:90%}}

.direction__sign{width:34px;height:34px;border-radius:17px;background:#737688;display:inline-block;margin-bottom:50px}.direction__icon{position:relative;top:6px;right:1px;font-size:1.4rem;display:inline-block}.direction__text{font-size:0.9rem;margin-left:15px;display:inline-block}

.forecast{margin:10px;padding:5px;color:#E7E7EB;background:#1E213A;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}.forecast .weather-icon img{width:50px}.forecast__temp p{display:inline-block}.forecast__low{color:#A09FB1;margin-left:10px}@media (min-width: 1000px){.forecast{-webkit-flex-direction:column;flex-direction:column}}

.search-panel{background:#1E213A;height:100vh;z-index:10;width:100%;position:fixed;top:0;left:0;overflow:scroll}.search-panel__wrapper{padding:20px}.search-panel__close{width:30px;height:30px;position:relative;margin:0 0 30px auto;cursor:pointer}.search-panel__cross{top:50%;-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.search-panel__cross,.search-panel__cross::after{width:28px;height:3px;border-radius:2px;background:#E7E7EB;position:absolute}.search-panel__cross::after{content:'';left:0;-webkit-transform:rotate(90deg);transform:rotate(90deg)}.search-panel__form{width:100%}.search-panel__form form{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.search-panel__input{padding:0 10px;height:38px;width:60%;border:solid 2px #E7E7EB;background:#1E213A;color:#E7E7EB;font-family:'Raleway', sans-serif}.search-panel__input::-webkit-input-placeholder{color:#A09FB1}.search-panel__input:-ms-input-placeholder{color:#A09FB1}.search-panel__input::placeholder{color:#A09FB1}.search-panel__search-button{height:42px;width:30%;max-width:200px;margin-left:5px;border:none;color:#E7E7EB;background:#3C47E9;cursor:pointer;font-family:'Raleway', sans-serif;font-size:1rem}.search-panel__search-button:hover{background:#2c36bd}.search-panel__search-button:active{background:#1a24a7}.search-panel__location{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;text-align:left;cursor:pointer;box-sizing:content-box;margin:10px 0;border:solid 2px rgba(0,0,0,0)}.search-panel__location p{margin:15px}.search-panel__location:hover{border:solid 2px #6E707A;transition:0.2s}.search-panel__location:hover .search-panel__location-icon{color:#6E707A;transition:0.2s}.search-panel__location-icon{position:relative;top:-2px;color:rgba(0,0,0,0)}.active-panel{-webkit-animation:appear 0.3s;animation:appear 0.3s}.inactive-panel{-webkit-animation:disappear 0.3s forwards;animation:disappear 0.3s forwards}@-webkit-keyframes appear{from{display:none;-webkit-transform:translateX(-100%);transform:translateX(-100%);z-index:10}to{display:block;-webkit-transform:translateX(0);transform:translateX(0);z-index:10}}@keyframes appear{from{display:none;-webkit-transform:translateX(-100%);transform:translateX(-100%);z-index:10}to{display:block;-webkit-transform:translateX(0);transform:translateX(0);z-index:10}}@-webkit-keyframes disappear{from{display:block;-webkit-transform:translateX(0%);transform:translateX(0%)}to{display:none;-webkit-transform:translateX(-100%);transform:translateX(-100%)}}@keyframes disappear{from{display:block;-webkit-transform:translateX(0%);transform:translateX(0%)}to{display:none;-webkit-transform:translateX(-100%);transform:translateX(-100%)}}:-moz-focusring{outline:none}@media (min-width: 1000px){.search-panel{width:25%}}.initial{display:none}

body{margin:0}.app,body,#root,html{margin:0;padding:0;color:#E7E7EB;text-align:center;font-family:'Raleway', sans-serif;background:#100E1D}.today{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-evenly;justify-content:space-evenly;height:100vh;background:#1E213A;margin:0}.today__buttons{padding:0 25px;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.today__search{position:relative;top:-15px;width:150px;height:28px;background:#6E707A;box-shadow:0px 4px 4px rgba(0,0,0,0.25);cursor:pointer;padding-top:5px}.today__position{height:34px;width:34px;border-radius:17px;background:#6E707A;box-shadow:0px 4px 4px rgba(0,0,0,0.25);cursor:pointer}.today__position p{position:relative;top:-19px}.today__icon{position:relative}.today__icon :nth-child(2){opacity:0.1;background-image:url(/weather-app/static/media/Cloud-background.f88244c2.png);background-size:cover;background-position:center;width:100%;height:320px;position:absolute;top:-40px}.today__temp{margin:0.5rem;font-size:6rem}.today__temp-unit{font-size:2.5rem}.today__weather,.today__date,.today__location,.today__temp-unit{color:#A09FB1}.today__weather{font-size:2rem;margin:1rem}.today__date,.today__location{margin:0.5rem}.unit-buttons{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}.unit-buttons p{height:34px;width:34px;border-radius:17px;font-weight:700;box-shadow:0px 4px 4px rgba(126,126,126,0.25)}.unit-buttons p span{position:relative;top:8px}.unit-buttons .active{background:#E7E7EB;color:#100E1D;pointer-events:none}.unit-buttons .active:hover{background:#d4d4db}.unit-buttons .active:active{background:#afafbd}.unit-buttons .inactive{background:#6E707A;cursor:pointer}.content{margin:30px 0}.forecasts{margin-bottom:50px}@media (min-width: 1000px){.app{display:-webkit-flex;display:flex;padding-left:25%}.today{width:25%;position:fixed;left:0}.unit-buttons{-webkit-justify-content:flex-end;justify-content:flex-end}.unit-buttons p{margin-right:30px}.content{max-width:1200px;width:75%;margin:auto;height:100vh;float:right;right:0;top:0}.highlights{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}.forecasts{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.forecasts .forecast{width:18%;height:200px}}@media (min-width: 700px){.highlights{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-flex-wrap:wrap;flex-wrap:wrap}}

